.main-caption {
    margin-top: 40px;
}



.iclist {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 40px;

    &__check {
        display: flex;
        align-items: center;
        margin-bottom: 15px;

        span img {
            width: 35px;
            height: 35px;
            margin-right: 10px;
        }
    }



}

.btn-blue {
    color: #fff;
    background: $color-blue;
    padding: 30px 30px;
    border-radius: 4px;
    text-decoration: none;
    transition: all .3s;
    cursor: pointer;
    text-align: center;
    font-weight: 500;
    display: inline-block;

    &:hover {
        background: darken($color-blue, 10%);
    }
}

.btn-main {
    display: block;

}

.ic_btn {
    width: 15px;
    height: 15px;
    fill: #fff;
    margin-left: 10px;
}

.btn-product {
    padding: 30px 40px;

}

.main-text {
    color: #455b70;
    font-weight: 600;
    line-height: 1.5;
    font-size: 18px;
    margin-bottom: 20px;
}

@media only screen and (max-width : 1200px) {

    .btn-main {
        display: inline-block;
        margin-bottom: 20px;

    }

}


@media only screen and (max-width : 540px) {
    .main-caption {
        margin-top: 0px;

        h1 {
            margin-top: 0;
        }
    }


    .btn-cons {
        padding: 30px 10px;
        width: 100%;

    }


    .iclist {
        font-size: 14px;
        font-weight: 400;
        margin-bottom: 40px;

        &__check {
            margin-bottom: 10px;

            span img {
                width: 15px;
                height: 15px;

            }
        }
    }

    .main-text {
        font-weight: 400;
        font-size: 14px;
    }
}
