.bluepart {
    background: $color-blue;
    padding: 60px 0 320px 0;

    h2 {
        color: #fff;
        margin-bottom: 30px;
    }

    &__btns {
        display: flex;
        align-items: center;
    }

    &__txt {
        margin-left: -40px;
        color: #fff;
        font-size: 19px;
        font-weight: 700;

    }

    &__ic {
        fill: #fff;
        height: 50px;
        width: 50px;
        margin-left: 20px;
    }

    &__wrp {
        position: relative
    }

    &__img {
        position: absolute;
        top: 0;
        right: 0;
    }
}


@media only screen and (max-width : 1200px) {

    .bluepart {
        &__img {
            width: 50%;
        }

    }

}


@media only screen and (max-width : 540px) {

    .bluepart {
        padding: 40px 0 160px 0;

        &__img {
            position: relative;
            width: 100%;
            z-index: 0;
        }

    }

}
