.top-part {
    border-bottom: 1px solid $color-line;
    transition: top 1s;
    top: -80px;
    z-index: 60;
}

.fixed {
    background: #fff;
    position: fixed;
    width: 100%;
    border-bottom: 1px none;
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.03);
    top: 0px;
    transition: top 1s;
}

.mfixed {
    margin-top: 80px;
}





.ellips {
    position: absolute;
    right: 5%;
    z-index: -30;
    height: 820px;
    width: 820px;
    border-radius: 50%;
    border: 130px solid #c6d0da;
    top: -430px;

}

.header-wrp {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;

}

.fixed .header-wrp {
    padding: 10px 0;
}

.logo {
    display: flex;
    align-items: center;

    a {
        font-size: 25px;
        font-weight: 700;
        margin-right: 20px;
        color: $color-blue;

        span {
            color: $color-heading;
        }
    }

    &__dscr {
        color: $color-font;
        font-size: 16px;
        font-weight: 400;
        line-height: 1.4;
    }
}

.fixed .logo__dscr {
    font-size: 14px;
}

.contacts {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: end;

    &__phone a {
        font-weight: 700;
        font-size: 22px;
        color: $color-heading;
        margin-bottom: 5px;
        display: block;
    }

    svg {
        fill: $color-blue;
        height: 20px;
        width: 20px;
    }

    .header-mail {
        color: $color-blue;
        display: block;
    }
}

@media only screen and (max-width : 768px) {
    .logo {
        &__dscr {

            font-size: 12px;

        }
    }

}

@media only screen and (max-width : 766px) {
    .contacts {
        display: none;
    }

    .header-wrp {
        display: flex;
        justify-content: flex-start;

    }

    .logo {

        a {
            font-size: 18px;

        }

        &__dscr {

            font-size: 10px;

        }
    }

    .fixed .logo__dscr {
        font-size: 9px;
    }

}



@media only screen and (max-width : 360px) {

    .fixed .logo__dscr {
        font-size: 8px;
    }


}
