.tizers {
    padding: 70px 0;
    background: #fff;
    margin: 30px 0;

}

.tizer {
    display: flex;

    &__ic {
        width: 60px;
        height: 60px;
        fill: $color-blue;
        margin-right: 20px;
        flex-basis: 20%;
    }

    &__info {
        flex-basis: 80%;
    }

    &__info p {
        font-size: 16px;
        font-weight: 300;
    }

    &__info h4 {
        font-size: 21px;
        margin-bottom: 20px;
        font-weight: 700;
    }


}

@media only screen and (max-width : 992px) {
    .tizers {
        padding: 30px 0;
    }

    .tizer {
        display: flex;
        margin-bottom: 20px;

        &__ic {
            width: 40px;
            height: 40px;
        }

        &__info p {
            font-size: 16px;
        }

        h4 {
            font-size: 16px;
            margin-bottom: 10px;

        }


    }

}

@media only screen and (max-width : 540px) {
    .tizer {
        &__info p {
            font-size: 14px;
        }
    }

}
