.catalog {
    padding: 78px 0;
}


.catalog_dscr {
    padding-left: 30px;
    border-left: 5px solid $color-blue;
    margin-bottom: 30px;

    p {
        line-height: 1.5;
        margin-bottom: 20px;
    }
}

.pr-prev,
.pr-next {
    border: none;
    padding: 10px 15px;
    background: $color-blue;
    font-size: 20px;
    transition: all .3s;
    color: #fff;

    &:hover {
        opacity: .7;
    }
}

.catalog_slider {
    margin: 20px -15px;

}

.item {
    padding: 20px;
    margin: 0 15px;
    background: #fff;


    &__img {
        height: 132px;
        margin-bottom: 30px;

        a {
            display: flex;
            justify-content: center;
            height: 100%;
        }

        img {
            max-height: 100%;
        }
    }

    &__title {
        font-size: 21px;
        font-weight: 700;
        margin-bottom: 20px;
        height: 55px;
        display: block;
        overflow: hidden;
        line-height: 1.3;
        text-decoration: none;
        color: $color-font;
    }

    &__info {
        display: flex;
        justify-content: space-between;
    }

    &__code {
        color: #b7b9bb;
        font-size: 13px;
        font-weight: 300;
    }

    &__sticker {
        color: #fff;
        font-size: 13px;
        padding: 5px 10px;
        border-radius: 4px;

        &--green {
            background: #37b656;
        }

        &--red {
            background: #df5f5f;

        }


    }

    &__buy {
        display: flex;
        justify-content: space-between;

    }

    &__prices {
        display: flex;
        flex-direction: column;
    }

    &__oldprice {
        color: #627078;
        font-size: 15px;
        text-decoration: line-through;
        margin-bottom: 10px;
        font-weight: 300;
    }

    &__newprice {
        font-size: 20px;
        font-weight: 700;
    }

    &__btn {
        padding: 15px 30px;
        display: block;
        margin-bottom: 20px;

        &:hover {
            color: #fff;
        }


    }
}

.card-data {
    padding-top: 30px;

    li {
        min-width: 260px;

        &:not(:last-child) {
            margin-bottom: 10px;

            span {
                &:first-child {
                    margin-right: 50px;
                    /*  color: #898989;*/

                }

            }

        }
    }

    .card-data__row {
        .card-data__list {
            display: inline-block;
            margin-right: 15px;
            margin-bottom: 30px;

            .dotted {

                display: flex;
                justify-content: space-between;
                align-items: flex-end;
                position: relative;

                span {
                    z-index: 2;
                    background: #fff;
                    font-size: 15px;
                }

                &:after {
                    content: '';
                    display: block;
                    width: 100%;
                    border-bottom: 1px dotted #e5e5e5;
                    position: absolute;
                    left: 0;
                    bottom: 1px;
                    z-index: 1;
                }

            }
        }

    }

}

.catalog-item {
    margin: 0 0 30px 0;
}

@media only screen and (max-width : 768px) {
    .catalog {
        padding: 25px 0;
    }
}
