//прижимаем футер

.page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}



.footer {
    margin-top: auto;
    background: $color-blue;
    text-align: center;

    &__wrp {
        padding: 40px 0;
    }

    &__title {
        font-size: 16px;
        line-height: 1.5;
        color: #fff;
        opacity: .6;
        margin-bottom: 10px;
    }

    &__ic {
        fill: #fff;
        width: 30px;
        height: 20px;
        margin-bottom: 5px;

    }

    &__lnk {
        font-size: 18px;
        text-transform: uppercase;
        font-weight: 600;
        color: #fff;
        display: block;
        margin-bottom: 15px;
        cursor: pointer;

        &:hover {
            color: #fff;
            opacity: .7;
        }
    }

    &__center {
        background: darken($color-blue, 5%);
    }

    &__cont {
        padding-right: 15px !important;
    }

}

.bottom-line {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    line-height: 1.3;
    font-size: 13px;

    a {
        color: $color-font;
    }

    &__rights {
        margin: 7px 0;
    }

    &__delosait {
        margin: 7px 0;

    }

}
