.whitepart {
    display: flex;
    align-items: flex-end;
    margin-top: -180px;
    margin-bottom: 90px;

    &__stages {
        background: #fff;
        box-shadow: 5px 5px 30px #56565612;
        padding: 70px 50px 0 50px;
        overflow: hidden;
        margin-right: 60px;
        flex-basis: 60%;
        overflow: hidden;
    }

    &__btns {
        flex-basis: 40%;

        p {
            margin-bottom: 20px;
        }
    }

    &__stage {
        display: flex;
        position: relative;
        margin-bottom: 40px;


    }

    &__number {
        font-size: 147px;
        font-family: 'Akrobat', sans-serif;
        color: #f6f6f6;
        font-weight: 800;
        margin-left: -80px;
        flex-basis: 20%;
        margin-bottom: 20px;
        display: block;

    }

    &__stagetxt {
        line-height: 1.6;
        padding-left: 30px;
        flex-basis: 80%;


        .bord {
            display: block;
            width: 100px;
            height: 4px;
            background: #30ed91;
            margin-top: 30px;

        }
    }

    .btn-tr {
        padding: 30px 70px;
        border: 1px solid $color-blue;
        color: $color-blue;
        font-weight: 600;
        text-decoration: none;
        display: inline-block;
        cursor: pointer;
        transition: all .3s;

        &:hover {
            color: #fff;
            background: $color-blue;
        }
    }


}

.wrap {
    position: relative;
    overflow: hidden;

}

.dark-circle {
    width: 650px;
    height: 650px;
    border: 90px solid #c6d0da;
    position: absolute;
    right: -300px;
    bottom: 0;
    border-radius: 50%;
    z-index: -10;
}

@media only screen and (max-width : 768px) {
    .whitepart {
        display: flex;
        align-items: flex-end;
        margin-top: -180px;
        margin-bottom: 40px;
        flex-wrap: wrap;

        &__stages {
            padding: 50px 20px 0 20px;
            margin-right: 0px;
            flex-basis: 100%;
            margin-bottom: 20px;
            z-index: 10;

        }

        &__btns {
            flex-basis: 100%;

            p {
                margin-bottom: 20px;
            }
        }

        &__stage {
            display: flex;
            position: relative;
            margin-bottom: 20px;


        }

        &__number {
            font-size: 107px;


        }

        &__stagetxt {
            line-height: 1.6;
            padding-left: 50px;
            flex-basis: 110%;

        }

        .btn-tr {
            padding: 30px 0px;
            width: 100%;
            text-align: center;

        }

    }
}
