.heading {
    background: $color-blue;
    padding: 30px 0px 90px 0;

    h1 {
        font-family: 'Akrobat', sans-serif;
        font-size: 38px;
        color: #fff;
        line-height: 1.3;
        margin-bottom: 20px;
        margin-top: 0;
    }

}

.breadcrumbs {
    li {
        display: inline-block;
        font-size: 15px;
        color: #ffffffa6;

        a {
            color: #fff;
            text-decoration: none;

            &:hover {
                color: #ffffffad;
            }
        }
    }

    li + li {
        &::before {
            color: #ffffffad;
            content: "/";
            padding: 0 5px;
        }

    }
}
