.navigation {
    position: fixed;
    display: flex;
    flex-direction: column;
    margin-right: -50px;
    margin-top: 20px;
    z-index: 100;


    &__btn {
        background: $color-blue;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-bottom: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        transition: all .3s;
        border: 2px solid #f6f9fc;

        &:hover {
            background: darken($color-blue, 10%);
        }
    }
}

.ic-nav {
    width: 20px;
    height: 20px;
    fill: #fff;
}

.container-nav {
    position: relative;
    display: flex;
    justify-content: flex-end;
}

@media only screen and (max-width : 992px) {
    .navigation {
        position: fixed;
        display: flex;
        flex-direction: column;
        margin-right: 0px;
        margin-top: 20px;
        right: 12px;


        &__btn {
            background: $color-blue;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            margin-bottom: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }

    .ic-nav {
        padding: 2px;

        fill: #fff;
    }

}
